@import 'normalize';
@import 'colors';
@import 'fonts';
@import 'media';

body {
  margin: 0;
  padding: 0;
  font-family: $main-font;
  font-size: 14px;
  font-weight: 500;
  line-height: 140%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $bc-main;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
}
